import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Location } from '../lib/definitions';
import type { RootState } from './Store';
import { apiSlice } from './apiSlice';
// Create an initial state value for the reducer, with Location type
const initialState: Location[] = [];

// Create the slice and pass in the initial state
const locationsSlice = createSlice({
	name: 'locations',
	initialState,
	reducers: {
		locationAdded: (state, action: PayloadAction<Location>) => {
			state.push(action.payload);
		}
	},
	extraReducers: (builder) => {
		builder.addMatcher(apiSlice.endpoints.getLocations.matchFulfilled, (state, action) => {
			//state = action.payload;
			state.push(...action.payload);
		});
		builder.addMatcher(apiSlice.endpoints.addLocation.matchFulfilled, (state, action) => {
			state.push(action.payload);
		})
	}
});

// Export the generated reducer as the default export
export default locationsSlice.reducer;

// Export the generated action creators
export const { locationAdded } = locationsSlice.actions;

export const selectAllLocations = (state: RootState) => state.locations;
